<template>
    <div class="pick">
      <div class="nav">
        <div class="nav_left" @click="backHome(indexa)">
          <div ><img src="../../assets/image/back.png" alt="" class="ico_earth"></div>
        </div>
        <div class="ico_earth_a">
          <span >收藏</span>
        </div>
        <div class="nav_right">
          <span class="ico_pick">关于我们</span>
        </div>
      </div>
      <div class="swiper">
        <div class="swiper-container">
          <div class="swiper-wrapper" >
            <div class="swiper-slide" v-for="(picklist, index) in pickList" :key="index">
              <div class="slide" v-for="(pickOne , index) in picklist" :key="index" @click="ShowDetails(pickOne)">
                <div class="top">
                  <img :src="pickOne.img" alt=""  class="topa">
                </div>
                <span>{{pickOne.texta}}</span></div>
            </div>

          </div>
          <!-- 如果需要分页器 -->
          <div class="swiper-pagination"></div>


        </div>
      </div>
    </div>
</template>
<script>
  import Swiper from 'swiper'
  import pickDetails from '../pickDetail/pickDetails.vue'
  import homepage from '../homepage/homepage.vue'
  import $ from 'jquery'
  export default {
    name: 'homepage',
    data () {
      return {
        pickList:[],
        indexa:this.$route.query.index,
//        token: window.goofypapaToken()
      }
    },


    mounted(){
//      window.onload = function () {
//        if(typeof( goofypapaGame ) != "undefined" && goofypapaGame ){
//          this.token = window.goofypapaToken
//        }
//      }

//        if(typeof( goofypapaGame ) != "undefined" && goofypapaGame ){
//            this.token = window.goofypapaToken
//        }


        if( window.android){
          window.android.setScreenOrientation(0)
          this.token = window.android.getUserToken()
        }


      var that = this
      this.$nextTick(()=>{
        var mySwiper =  new Swiper ('.swiper-container', {
//          slidesPerView: 2,
//          spaceBetween: 30,
//          slidesPerGroup:2,
//          loopFillGroupWithBlank: true,
          direction: 'vertical',
//          loop: true,
          // 如果需要分页器
          on:{
            slideChange: function(){
              console.log(mySwiper.realIndex)
            },
          },


          pagination: '.swiper-pagination',
          // 如果需要前进后退按钮
          nextButton: '.swiper-button-next',
          prevButton: '.swiper-button-prev',
          // 如果需要滚动条
          scrollbar: '.swiper-scrollbar',
          initialSlide :0,
          observer:true,//修改swiper自己或子元素时，自动初始化swiper
          observeParents:true,//修改swiper的父元素时，自动初始化swiper
        })

            $.ajax({
            type: "post",
            url: "http://www.dadpat.com/favorite/list.do",
            dataType: "jsonp",
              headers:{
                Authorization:that.token
              },
            data:{
              targetType:"earthApp"
            },
            async: true,
            success: function (data) {

//              切割字符串
              var strAll = data.data
              var strsplit = []
              for(var i = 0;i<strAll.length;i++){
                strsplit.push(strAll[i].targetId.split('///'))
              }


              var shouji = []

              for(var k = 0;k<strsplit.length;k++){
               console.log( strsplit[k][0])
               console.log( strsplit[k][1]*1)

                $.ajax({
                  type: "post",
                  url: "http://www.dadpat.com/file/json.do",
                  data: {"path":"app/earth/"+strsplit[k][0],"name":strsplit[k][1]},
                  dataType: "jsonp",
                  async: true,
                  success: function (data) {

                    var pickdata = data.data
                    shouji.push(pickdata)

                    var split_array =(data,len)=>{
                      let arr_length = data.length;
                      let newArr = [];
                      for(let i=0;i<arr_length;i+=len){
                        newArr.push(data.slice(i,i+len));
                      }
                      return newArr;
                    }

                    let result = split_array(shouji, 2);
                    console.log(result)
                    that.pickList = result
                  }
                })
              }






//每两个一组数据

            }
          })
//            $.ajax({
//            type: "post",
//            url: "http://www.dadpat.com/favorite/list.do",
//            dataType: "jsonp",
//            data:{
//              targetType:"earthApp"
//            },
//            async: true,
//            success: function (data) {
////              that.pickList = data.data
//              var picklist_ = []
//              var picklist = data.data
//              for(var i=0;i<picklist.length;i++){
//                var abc =   picklist[i].targetId
//                picklist_.push(abc)
//              }
//
//            var  split_array=(data,len)=>{
//                let arr_length = data.length;
//                let newArr = [];
//                for(let i=0;i<arr_length;i+=len){
//                  newArr.push(data.slice(i,i+len));
//                }
//                return newArr;
//              }
//
//              var result = split_array(picklist_, 2);
//
//              that.pickList = result
//            }
//          })

      })

    },

    methods: {
       ShowDetails(targetId){
        this.$router.push({
          path: '/pickDetails',
          component: pickDetails,
          query: {pickOne: targetId}
        })

      },

      backHome(indexa){
        this.$router.push({
          path: '/',
          component: homepage,
          query:{index_:indexa}
        })
      }

    },
    components:{
      pickDetails,homepage
    }
  }
</script>
<style scoped>
  @import "../../../static/css/iphonex.css";
  .pick{
    width: 100%;
    height: 100%;
    background: url("../../assets/image/background.png");
    background-size:cover;
  }
  .nav{
    line-height: 3.1rem;
    font-size: .7rem;
    color:#fff;
    height: 2.87rem;
    width: 100%;
    background: #000;
    display: flex;
    justify-content:space-between;
  }
  .ico_earth{
    width: 1.2rem;
    height: 1.2rem;
    margin-top: 1rem;
    margin-left: .7rem;
    /*float: left;*/
  }
  .ico_earth_a{
    margin-left: 1.7rem;
    /*float: left;*/
    /*line-height: 3.1rem;*/
    /*margin-left: 0.1rem;*/
  }
  .ico_pick{

    /*margin-left: 7rem;*/
    margin-right: .7rem;
  }

  .swiper{
    width: 100%;
    height: 25.58rem;
  }
  .swiper-container {
    width: 100%;
    height: 25.58rem;
  }

 .top{
    margin-top: 2rem;
    margin-left: 4.08rem;
    width: 8rem;
    height: 8rem;
    border-radius: 50%;
   position: relative;
   z-index: 200;
   overflow: hidden;
   margin-bottom: .2rem;
  }
  .topa{
    height: 8rem;
    margin:0 auto;
    position: relative;
    z-index: -100;
    background: rgba(255,255,255,.2);
  }
  .slide{
    display: flex;
    flex-direction: column;
    margin-bottom: 1.5rem;
  }
  .slide1{
    margin-top: 1.5rem;
  }

  .slide span{
    text-align: center;
    font-size: .7rem;
  }
</style>



